              <p class="py-4">In addition to the database, this website contains <a class="text-blue-600 dark:text-blue-500 hover:underline" href="#">resources</a> to enhance users’ understanding of printed images and inspire further <a class="text-blue-600 dark:text-blue-500 hover:underline" href="#">research</a> in the field. These resources include guides on using images in undergraduate and post-graduate <a class="text-blue-600 dark:text-blue-500 hover:underline" href="#">dissertations</a>, overviews of current research, and introductions to the different <a class="text-blue-600 dark:text-blue-500 hover:underline">genres</a> of single sheet prints and the various <a class="text-blue-600 dark:text-blue-500 hover:underline" href="#">techniques</a> of printmaking. <a class="text-blue-600 dark:text-blue-500 hover:underline" href="#">‘Prints in Focus’</a> unpicks individual prints, contextualising them in the society and culture of early modern Britain</p>
