import { createWebHistory, createRouter } from "vue-router";
import HomeView from './views/HomeView'
import AboutView from './views/AboutView'
import PrintView from './views/PrintView'
import BlogView from './views/BlogView'
import SearchView from './views/SearchView'
import ResourcesView from './views/ResourcesView'
import ResearchView from './views/ResearchView.vue'
import ResultsViewPagination from './views/ResultsViewPagination'
import ContactView from './views/ContactView'
import ProducerView from './views/ProducerView'
import AdditionalProducerView from './views/AdditionalProducerView'
import GenresView from "./views/GenresView.vue";
import TechniquesView from "./views/TechniquesView.vue";


const routes = [
    {
      path: '/',
      name: 'Home',
      component: HomeView,
    },
    {
      path: '/about',
      name: 'About',
      component: AboutView,
    },
    {
      path: '/prints',
      name: 'Prints',
      component: PrintView,
    },
    {
      path: '/blog',
      name: 'Blog',
      component: BlogView,
    },
    {
      path: '/contact',
      name: 'Contact',
      component: ContactView
    },
    {
      path: '/search',
      name: 'Search',
      component: SearchView
    },
    {
      path: '/research',
      name: 'Research',
      component: ResearchView
    },
    {
      path: '/resources',
      name: 'Resources',
      component: ResourcesView
    },
    {
      path: '/resources/genres',
      name: 'Genres',
      component: GenresView
    },
    {
      path: '/resources/techniques',
      name: 'Techniques',
      component: TechniquesView
    },
    {
      path: '/resultsViewPagination',
      name: 'Results',
      component: ResultsViewPagination
    },
    {
      path: '/producerView/:producerName',
      name: 'Producer',
      component: ProducerView
    },
    {
      path: '/additionalProducerView/:producerName',
      name: 'AdditionalProducer',
      component: AdditionalProducerView
    }  
 ]; 

 // create singleton router object
const router = createRouter({
    history: createWebHistory(),
    routes,
  });

  export default router;