            <h2 class="mt-2 text-2xl font-bold tracking-tight text-gray-900 sm:text-2xl">Woodcut</h2>
            <div class="mt-4 text-left">
              <p class="py-4">Invented in China in the 9th century and the oldest of the four techniques. Woodblocks were used in Europe from the 13th century, with the first prints on paper appearing in the late-14th. </p>
              <p class="py-4">IBlocks were made from soft wood around an inch thick. The design would be drawn on it (or on paper glued to the surface). A woodcutter would cut away the areas around the lines of the design before applying ink to the remaining raised area using a dabber. The image was transferred to paper by stamping it by hand, by rubbing the back of paper laid on top of the block, or, from the 1450s onwards, by use of a press. Because the presses were also used for movable type, it was possible to print images and letterpress text together on a single page of a book. This was not possible in the intaglio processes. Despite this advantage, woodcuts declined in the 17th century relative to intaglio prints that could produce finer and more precise images. </p>
              <p class="py-4">Distinguishing a woodcut from one of the other techniques is straightforward. A woodcut will lack a platemark (the mark around the edges of intaglio prints); lines are more likely to be uneven when compared to intaglio prints; and by it is possible to see that lines have been achieved by cutting around them.</p>
            </div>
