<script>
const path = process.env.VUE_APP_ROOT_API

import Slider from '@vueform/slider'
import axios from 'axios'
import router from '../router'
import * as dataService from '../services/dataService'
import { FwbSpinner } from 'flowbite-vue'
import { useVuelidate } from '@vuelidate/core'
import { useBPIStore } from '@/store/BPIs'
import { useImageDataStore } from '@/store/imagedata'
import { required, minLength, maxLength, alpha } from '@vuelidate/validators'


export default {

  setup: () => ({ v$: useVuelidate() }),
  components: {
    Slider,
    FwbSpinner
  },
  data: () => ({
    dateSlider: {
      value: [1600, 1700],
      min: 1550,
      max: 1815
    },
    // _item vars match validations
    subject_search_item: '',
    event_search_item: '',
    person_search_item: '',
    producer_search_item: '',
    producer_name: '',
    technique: '',
    search_subject_data: [],
    search_subject_index_data: [],
    search_event_data: [],
    search_person_data: [],
    search_producer_data: [],
    search_techniques: [],
    all_images: [],
    prodList: { 
        'Barlow': 'barlow', 
        'Gaywood': 'gaywood', 
        'Hollar': 'hollar', 
        'Kneller': 'kneller',
        'Lely': 'lely',
        'White': 'white'
    },
    techniqueList: {
        'drypoint': 'Drypoint',
        'handcoloured': 'Hand-coloured',
        'letterpress': 'Letterpress', 
        'rubbing': 'Rubbing',
        'woodcut': 'Woodcut',
        'stipple' : 'Stipple'
    },
    inProdList: false,
    showDateFilter: false,
    spinnerShow: false,
    resultShow: false,
    resultsFound: false,
    producerDateCheckbox: null,
    eventDateCheckbox: null,
    producerListError : null
  }), 
  validations () {
    // matches v-model variables
    return {
      subject_search_item: { required, minlengthValue: minLength(3), maxLengthValue: maxLength(50), alpha }, 
      event_search_item: { required, minLengthValue: minLength(3), maxLengthValue: maxLength(50), alpha },
      person_search_item: { required, minLengthValue: minLength(3), maxLengthValue: maxLength(50), alpha },
      producer_search_item: { required, minLengthValue: minLength(3), maxLengthValue: maxLength(50), alpha }
    }
  },
  methods: {
    viewResultsPage() {
      router.push('/resultsViewPagination')
    },
    viewProducerPage() {
      router.push({
        name: 'Producer',
        params: {
          producerName: this.producer_name
        }
      })
    },
    setShow() {
      setTimeout(() => {
        this.resultShow = true;
        this.spinnerShow = false;
      }, 3000);
    },
    clearSearches() {
      localStorage.clear();
      const bpiStore = useBPIStore();
      const imageDataStore = useImageDataStore();
      bpiStore.clearBPI();
      bpiStore.clearBPIList();
      bpiStore.clearBPIPages();
      imageDataStore.clearImageDetails();
      this.data_bpis = [];
      this.subject_search_item = '';
      this.event_search_item = '';
      this.person_search_item = '';
      this.producer_search_item = '';
      this.search_subject_data = [];
      this.search_subject_index_data = [];
      this.search_event_data = [];
      this.search_person_data = [];
      this.search_producer_data = [];
      this.search_techniques = [];
      this.resultShow = false;
      this.resultsFound = false;
      this.showDateFilter = false;
      this.producerDateCheckbox = null;
      this.eventDateCheckbox = null;
      this.producerListError = null;
      this.v$.$reset();
    },
    async handleSearchRequests(event, type) {
      event.preventDefault();

      // use pinia store
      const bpiStore = useBPIStore();
      const imageDataStore = useImageDataStore();
      // clear out any previous data in store
      bpiStore.clearBPI();
      imageDataStore.clearImageDetails();

      if(type === 'event'){
        const result = await this.v$.event_search_item.$validate();
        if (!result) {
          // show error message
          return;
        }
        // reset between search requests
        this.search_event_data = [];
      }
      else if(type === 'person'){
          const result = await this.v$.person_search_item.$validate();
        if (!result) {
          // show error message
          return;
        }
        // reset between search requests
        this.search_person_data = [];
      }
      else if (type === 'subject'){
        const result = await this.v$.subject_search_item.$validate();
        if (!result) {
          // show error message
          return;  
        }
        this.search_subject_data = [];
      }
      else if (type === 'producer'){
        const result = await this.v$.producer_search_item.$validate();
        if (!result) {
          // show error message
          return;  
        }
        this.search_producer_data = [];
        this.resultShow = false;
        if (this.checkInProdList(this.producer_search_item.toLowerCase())) {
          this.inProdList = true;
          return;
        }
      }
      // in case its a technique search
      this.search_techniques = [];
      // display changes
      this.removeMessage();
      this.resultShow = false;
      this.spinnerShow = true;

      // try to get results then stop the spinner
      try {
         await this.getImageResults(type); 
        }
        catch (err) {
          console.log('Error fetching data:', err)
        }
        finally {
          // add a timeout so that data has a chance to load into localstorage, even when network calls are done.
          this.setShow()
        }
    },
    async getImageResults(type){
      if(type === 'event'){
            if (this.showDateFilter === true) {
              await this.getImagesByDate('event',this.event_search_item, this.dateSlider.value);
            }
            else {
              await this.getImages('event', this.event_search_item);
            }
          }
          else if (type === 'person'){
              await this.getImages('person',this.person_search_item),
              await this.getImages('label',this.person_search_item)
          } 
          else if (type === 'subject'){
              await this.getImages('subject', this.subject_search_item);
              await this.getImages('subject_index', this.subject_search_item);
          }
          else if (type === 'producer'){
            if (this.showDateFilter === true) {
              await this.getImagesByDate('producer',this.producer_search_item, this.dateSlider.value);
            }
            else {
              await this.getImages('producer',this.producer_search_item);
            }
          }
          else if (type === 'technique'){
            await this.getImages('technique',this.technique);
          }
    },
    checkInProdList(item) {
      // search in the prodList
      if (Object.values(this.prodList).includes(item)) {
        this.producerListError = "Please use the 'Popular Producer/Printmaker' search.";
        return true;
      }
      this.producerListError = null;
      return false;
      
    },
    checkboxToggle() {
      if (this.showDateFilter == false) {
        this.showDateFilter = true;
      }
      else if (this.showDateFilter == true) {
        this.showDateFilter = false;
      }
    },
    resetDateSlider() {
      this.dateSlider.value = [1600, 1700]
    },

    // API functions
    async getImages(search_type, search_item) {
      try {
        let api = '';

        if(search_type === 'subject'){
          api = 'image_subject_search/';
        }
        else if (search_type === 'subject_index'){
          api = 'image_subject_search_by_index/';
        } 
        else if (search_type === 'event'){
          api = 'image_event_search/';
        } 
        else if (search_type === 'person'){
          api = 'image_person_search/';
        } 
        else if (search_type === 'label'){
          api = 'image_label_search/';
        } 
        else if (search_type === 'producer'){
          api = 'image_producer_search/';
        } 
        else if (search_type === 'technique'){
          api = 'image_technique_search/';
        } 
        // call the api
        const response = await axios.get(
          path + api + search_item
        )
        if(response !== null && response !== undefined){
          await this.handleResponse(response, search_item);
        }
      } catch (err) {
          console.log('Error fetching data:', err)
      }
    },
    async getImagesByDate(search_type, search_item, sliderValues) {
      try {
        let api = '';
        if (search_type === 'producer'){
          api = 'image_producer_search_date/';
        } 
        else if (search_type === 'event'){
          api = 'image_event_search_date/';
        } 
        // call the api
        const response = await axios.get(
          path + api + search_item + '/' + sliderValues[0] + '/' + sliderValues[1]
        )

        if(response !== null && response !== undefined){
          await this.handleResponse(response, search_item);
        }
      } catch (err) {
          console.log('Error fetching data:', err)
      }
    },
    async handleResponse(response, search_item) {

      let data_bpis = [];
      let search_data = [];

      if (!response.data.recordset || response.data.recordset.length === 0) {
        console.log('No ' + search_item + ' data');
      } 
      else {
        search_data = response.data.recordset;
       
        search_data.forEach((object) => {
          if (object.BPI_catKey !== null) {
            data_bpis.push(object.BPI_catKey)
          }
        })
        // TODO put in pinia action
        if (data_bpis.length !== 0) {
          await this.callDataService(data_bpis)
          this.resultsFound = true;
        }
      } 
    },
    removeMessage() {
      this.inProdList = false;
    },
    removeDuplicates(array){
      let unique = [];
      array.forEach(element => {
        if(!unique.includes(element)){
          unique.push(element)
        }
      });
      return unique;
    },
    validate(search_item){
      // check for null etc
      if(search_item !== 0 && search_item !== null && search_item !== undefined) {
        return true;
      }
      return false;
    },
    // handle writes to localstorage and pinia
    async callDataService(data_bpis){
      // use pinia store
      const bpiStore = useBPIStore();
      // add all BPIs to pinia store
      bpiStore.addToBPIArray(data_bpis); 
      bpiStore.updateBPIIndex(0);
      const myFirstChunk = bpiStore.getBPIChunk(0);
      const values = myFirstChunk.toString();
      // set bpi list of 20 results into pinia
      bpiStore.addToBPIList(values);

      // this section is for pagination 
      myFirstChunk.forEach(async bpi_num => {
        if (bpi_num !== null) {
          await dataService.getAllImageDetails(bpi_num); 
        }
      })

      bpiStore.BPI_array.forEach(async bpi_num => {
        if (bpi_num !== null) {
          await dataService.getAllImageDetails(bpi_num); 
        }
      })
    }
  }
}
</script>

